		</template>
		<a-table :columns="columns" :data-source="dataSource" :pagination="true">

			<a-space slot="members" slot-scope="members" :size="-12" class="avatar-chips">
				<template v-for="member in members">
					<a-avatar :key="member" size="small" :src="member" />
				</template>
			</a-space>

			<template slot="experiment" slot-scope="experiment">
				<h6 class="m-0">
					<img :src="experiment.logo" width="25" class="mr-10">
