<script>
  import Modal from "@/components/Modal/Modal"
  import moment from 'moment'
	export default ({
    components: {
      Modal
    },
		props: {
			dataSource: {
				type: Array,
				default: () => [],
			},
			columns: {
				type: Array,
				default: () => [],
			},
      changeChartData:{
        type: Function,
        require: true,
        default: null,
      }
		},
		data() {
			return {

				// Active button for the "Projects" table's card header radio button group.
				projectHeaderBtns: 'all',
			}
		},
    methods: {
      onCreateExp(){
        this.$refs.modal.showModal()
      },
      onEditFlow(id){
        this.changeChartData(id)
      },
      onCellChange(key, dataIndex, value) {
        const dataSource = [...this.dataSource];
        const target = dataSource.find(item => item.key === key);
        if (target) {
          target[dataIndex] = value;
          this.dataSource = dataSource;
        }
      },
      onDelete(key) {
        const dataSource = [...this.dataSource];
        // this.dataSource = dataSource.filter(item => item.key !== key);
        const target = dataSource.find(item => item.key === key);
        if (target) {
          target['completion'].label = '暂停';
          target['completion'].status = 'exception'
          this.dataSource = dataSource;
        }
      },
      addDataSource(name, members){
        let mems = [];
        members.forEach((item) => {
          mems.push("images/face-"+ item + ".jpg")
        })

        const new_data = {
              key: this.dataSource.length+1,
              experiment: {
                name: name,
                // logo: 'images/logos/logo-slack.svg',
              },
              members: mems,
              time: moment(new Date()).format("yyyy-MM-DD HH:mm"),
              completion: {
                label: 'stop',
                status: 'exception',
                value: 1,
              },
            }

        const dataSource = [...this.dataSource, new_data];
        console.log(        new_data)

        this.dataSource.push(new_data);

      },
    },
	})

</script>